.error-popup {
  width: 100vw;
  min-height: 100px;
  padding: 5px;
  background-color: rgba(255, 0, 0, 0.6);
  position: fixed;
  top: 0px;
  left: 0px;
  opacity: 0;
  transition: opacity 0.5s linear;
  z-index: -1;

  .error-popup__message {
    width: calc(100% - 80px);
    font-size: 20px;
    line-height: 40px;
    font-weight: 400;
    color: white;
    word-break: break-all;
  }

  .error-popup__close-btn {
    position: absolute;
    right: 25px;
    top: 25px;
    width: 50px;
    height: 50px;
    font-size: 50px;
    line-height: 50px;
    font-weight: 400;
    color: white;
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
  }
}

.error-popup_show {
  opacity: 1;
  z-index: 1;
}
